* {
    box-sizing: border-box;
    margin:0;
}

video {
    position:absolute;
    z-index:0;
    width:100%;
    height:100vh;
    object-fit: cover;
    margin:0;
    top:0px;
    left:0px;
    border:none;
    filter:brightness(60%) contrast(60%) invert(20%);
}

.App{
    display:inline-grid;
    grid-template-columns: auto;
    grid-template-rows: 120px 175px 195px;
    grid-gap:15px;
    width:100vw;
    height: 100vh;
    margin:0;
    padding:0;
    overflow: hidden;
    z-index: 2;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    justify-content: space-evenly;
    align-content: center;
}

.tag {
    font-size: small;
    font-weight: 500;
    position:relative;
    top:35px;
}
.credits {
    font-size: small;
    top:40px;
}

.blob {
    background-color: white;
    border-radius: 15px;
    padding: 10px;
    opacity: 0.8;
}

header {
    grid-column: 1 / span 2;
    font-size: 69px;
    white-space: nowrap;
    position: relative;
    height:fit-content;
    width: fit-content;
    margin: 0 auto;
}

button {
    opacity: 1;
    border-radius: 50px; 
    border: none;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; 
    font-weight: bolder; 
    background:white; 
    font-size: larger;
    height: 40px;
    width: 95px;
    transition:all 0.2s;
}

.arrow {
    line-height: 6px;
}

.main {
    font-size: medium;
}

button:hover {
    background: #ffb703;
}

.selectors { 
    position:relative;
    height:175px;
    width: 250px;
    font-size: 35px;
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 20px;
    margin: 0 auto;
    button {
        margin: 0 auto;
    }
    div {
        margin: 0 auto;
        grid-column: 1/ span 2; 
    }
    span { 
        margin: 0 auto; 
        grid-column: 1/ span 2; 
    } 
} 

#mainDisplay { 
    grid-column: 1 / span 2;
    padding: 10px; 
    width: fit-content; 
    height: 180px; 
    display: flex; 
    justify-content: center; 
    align-items: center; 
    flex-direction: column; 
    font-size: 55px; 
    margin: 0 auto;
    #timer-label {
        margin-top: 25px;
    }
} 

button:hover { 
    animation: pulse 1s; 
    box-shadow: 0 0 0 2em transparent; 
} 